.card {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  border: 1px solid #f3f3f3;
  background: $colorWhite;
  border-radius: 5px;
  width: 200px;
  padding: 33px 16px 20px;
  transition: transform 0.6s ease;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    width: 160px;
    height: 134px;
    display: flex;
    object-fit: cover;
    margin-bottom: 40px;
  }

  &__text {
    margin-bottom: 4px;
    display: flex;
    color: $colorText;
    font-family: inherit;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    transition: color 0.5s ease;

    &--hover:hover {
      color: #34568b;
    }
  }

  &__product-code {
    margin-bottom: 16px;
    color: $colorTextMini;
    font-family: inherit;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
  }

  &__reviews {
    margin-bottom: 24px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-shrink: 0;

    &-text {
      display: flex;
      justify-content: center;
      align-items: center;
      color: $colorText;
      text-align: right;
      font-size: 10px;
      font-style: normal;
      font-weight: 400;
      line-height: 14px;
    }

    &-stars {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-image: url(../images/star.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__price {
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;

    &-text {
      color: $colorTextMini;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
    }

    &-total {
      color: $colorText;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__button {
    font-weight: 700;
    padding: 12px;
    width: 100%;
    cursor: pointer;
    flex-shrink: 0;
    border: 1px solid $colorWhite;
    border-radius: 5px;
    background-color: $color-hover;
    font-family: Roboto, sans-serif;

    color: $colorWhite;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    line-height: 16px;
    text-transform: uppercase;
    transition: background-color 0.7s ease;

    &:hover {
      border-radius: 5px;
      border: 1px solid $color-hover;
      background-color: #fff;
      color: $color-hover;
    }
  }
}
